/*
*
* Typography
*/

//
// Body
//
body {
	font-family: $font-family-base;
	font-size: $font-size-base;
	line-height: $line-height-base;
	font-weight: $font-weight-base;
	color: $body-color;
	background-color: $body-bg;
}

//
// Headings
//
#{headings()} {
	margin-top: 0;
	margin-bottom: 0;
	color: $headings-color;

	a {
		color: inherit;

		&:hover {
			color: $primary;
		}
	}

	span {
		display: inline-block;
	}

	span[data-toggle='modal'] {
		border-bottom: 2px dashed rgba($primary, .3);
		cursor: pointer;
	}

	> span.icon {
		display: inline-block;
		margin-right: 5px;
	}
}

h1,
.heading-1 {
	font-size: 36px;
	line-height: $h1-line-height;

	@include media-breakpoint-up(md) {
		font-size: 46px;
	}

	@include media-breakpoint-up(lg) {
		font-size: 54px;
	}

	@include media-breakpoint-up(xl) {
		font-size: $h1-font-size;
	}
}

h2,
.heading-2 {
	font-size: 32px;
	line-height: $h2-line-height;

	@include media-breakpoint-up(md) {
		font-size: $h2-font-size;
	}
}

h3,
.heading-3 {
	font-size: 26px;
	line-height: $h3-line-height;

	@include media-breakpoint-up(md) {
		font-size: 30px;
	}

	@include media-breakpoint-up(xl) {
		font-size: $h3-font-size;
	}
}

h4,
.heading-4 {
	font-size: 22px;
	line-height: $h4-line-height;
	font-family: $font-family-base;
	font-weight: 300;

	@include media-breakpoint-up(xl) {
		font-size: $h4-font-size;
	}
}

h5,
.heading-5 {
	font-size: 18px;
	line-height: $h5-line-height;
	font-family: $font-family-base;
	color: $body-color;

	@include media-breakpoint-up(md) {
		font-size: $h5-font-size;
	}
}

h6,
.heading-6 {
	font-size: $h6-font-size;
	line-height: $h6-line-height;
	font-family: $font-family-base;
	font-weight: 600;
	letter-spacing: .2em;
	text-transform: uppercase;
}

.title-decorated {
	position: relative;
	padding-left: 40px;
	text-align: left;

	&::before {
		content: '';
		position: absolute;
		left: 0;
		top: .8em;
		width: 30px;
		border-bottom: 1px solid;
	}

	@include media-breakpoint-up(md) {
		padding-left: 70px;

		&::before {
			width: 50px;
			top: .7em;
			border-bottom-width: 2px;
		}
	}
}

.title-decorated-lg {
	@include media-breakpoint-up(md) {
		padding-left: 80px;

		&::before {
			width: 62px;
		}

		& + p {
			margin-top: 32px;
		}
	}
}

.font-family-base {
	font-family: $font-family-base;
}

//
// Emphasis
//
small,
.small {
	display: block;
	font-size: $font-size-sm;
	line-height: $line-height-sm;
}

mark,
.mark {
	padding: 3px 5px;
	color: $white;
	background: $primary;
}

strong {
	font-weight: 700;
	color: $gray-400
}

// Big text
.big {
	font-size: $font-size-lg;
	line-height: $line-height-lg;
	letter-spacing: .03em;
	font-weight: 300;
}

.lead {
	font-size: $lead-font-size;
	line-height: $lead-line-height;
	font-weight: $lead-font-weight;
}

code {
	padding: $code-padding-y $code-padding-x;
	border-radius: 0;
	font-size: $code-font-size;
	color: $code-color;
	background: $code-bg;
}

.text-large {
	font-family: $font-family-sec;
	font-size: 30px;
	font-weight: 500;
	letter-spacing: 0;
	line-height: 1.2;
	text-transform: uppercase;

	@include media-breakpoint-up(sm) {
		font-size: 55px;
	}

	@include media-breakpoint-up(md) {
		font-size: 68px;
	}

	@include media-breakpoint-up(lg) {
		font-size: 80px;
	}

	@include media-breakpoint-up(xl) {
		font-size: 100px;
	}
}

%context-dark {
	.text-large {
		color: $white;
	}
}

.text-extra-large {
	font-size: 100px;
	line-height: .8;
	font-weight: 800;

	@include media-breakpoint-up(md) {
		font-size: 130px;
		line-height: .7;
	}

	@include media-breakpoint-up(lg) {
		font-size: 150px;
	}

	@include media-breakpoint-up(xxl) {
		font-size: 180px;
	}
}

.wow-outer {
	span {
		display: block;
	}

	.wow > span {
		display: inline;
	}
}

//
// Paragraph
//
p {
	[data-toggle='tooltip'] {
		padding-left: .25em;
		padding-right: .25em;
		color: $primary;
	}

	[style*='max-width'] {
		display: inline-block;
	}

	a {
		color: inherit;
	}
}

p .text-width-1 {
	display: inline-block;
	max-width: 730px;
}

.text-width-2 {
	display: inline-block;
	max-width: 350px;
}


//
// Colors
//
html .page {
	.text-primary {
		color: $primary;
	}
}

html .page {
	.text-accent-dark {
		color: $gray-700;
	}
}


.page {
	.text-danger {
		color: $danger-color;
	}

	.text-gray-700 {
		color: $gray-700;
	}

	.text-color-1 {
		color: $color-1;
	}
}


%context-dark {
	.text-gray-700 {
		color: $white;
	}
}


.text-medium {
	font-size: 18px;
	@include media-breakpoint-up(xl) {
		font-size: 20px;
	}
}


